const uni = require('uni').default;














































import {
	mapActions
} from 'vuex'
import pageHead from '../../../components/page-head.vue'

const templateMessageUrl = `https://www.example.com/templateMessage`; //开发者自己的接口地址
export default {
	data() {
		return {
			title: 'templateMessage',
			formData: {
				address: 'T.I.T 造舰厂',
				time: '2017.01.09',
				name: '帝国歼星舰',
				serial: '123456789'
			},
			loading: false
		}
	},
	methods: {
		...mapActions(['getUserOpenId']),
		submitForm: async function (e) {
			var form_id = e.target.formId
			var formData = e.target.value

			console.log('form_id is:', form_id)

			this.loading = true
			try {
				const openid = await this.getUserOpenId()
				console.log('openid:::::' + openid)
				uni.request({
					url: templateMessageUrl,
					method: 'POST',
					data: {
						form_id,
						openid,
						formData
					},
					success: (res) => {
						console.log('submit form success', res)
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						})
						this.loading = false
					}

				})
			} catch (e) {
				this.loading = false
				console.log('submit form fail, errMsg is:', e)
			}
		}
	},
	components: {
		pageHead
	}
}



